<template>
	<div id="kefu-setting">
		<public-header title="顾客消息设置"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t border-0px">
				<van-cell title="当前客服状态" label="此处设置为配送员的客服状态" :value="deliveryer.kefu_status_cn" is-link :to="util.getUrl({path: '/pages/kefu/update', query: {type: 'kefu_status'}})" />
			</van-cell-group>
			<van-cell-group class="margin-10-t border-0px">
				<van-cell class="ellipsis" title="忙碌状态自动回复" :value="deliveryer.extra.kefu_busy_reply ? deliveryer.extra.kefu_busy_reply : '暂未设置'" is-link :to="util.getUrl({path: '/pages/kefu/update', query: {type: 'busy_reply'}})" />
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import iswitch from "@/components/switch"
	import {getData} from '@/controller/funcCommon'
	export default {
		data() {
			return {
				deliveryer: {
					kefu_status: 1,
					kefu_status_cn: '',
					extra: {
						kefu_busy_reply: ''
					}
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/kefu/setting/index',
					data: {},
					success: (res) => {
						that.deliveryer = that.util.extend(that.deliveryer, res.deliveryer);
					}
				})
			},
			jsToggleSwitch(data) {
				var that = this;
				var confirm = null;
				if(data.value == 0) {
					confirm = '<div class="text-center">顾客消息开启有助于<br>提高用户下单转化率<br>如当前有会话未完成，建议您处理后再关闭<br>关闭后不再接收消息，但历史记录仍然保存</div>';
				}
				this.util.jsToggleSwitch({
					vue: that,
					key: data.keys,
					value: data.value,
					url: 'delivery/kefu/setting/kefu_status',
					confirm: confirm,
					confirmButtonText: '关闭',
					data: {
						type: data.type,
						value: data.value
					}
				});
			},
		},
		mounted() {
			var that = this;
			that.onLoad();
		},
		components: {
			PublicHeader,
			iswitch
		},
	}
</script>

<style>
	#kefu-setting .status{
		border: 1px solid #fff;
	}
	#kefu-setting .status .iswitch{
		height: 24px;
	}
	#kefu-setting .status .tips{
		margin: 0 15px 9px 15px;
		padding: 10px;
		line-height: 1.4;
		font-size: 12px;
		background-color: rgba(51, 170, 252, 0.2);
		color: #33aafc;
	}
</style>


